
import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';


import {BioComponent} from './bio/bio.component';

import {PageAccueilComponent} from './page-accueil/page-accueil.component';
import {AppComponent} from './app.component';
import {PageContactComponent} from './page-contact/page-contact.component';
import {PageRealisationsComponent} from './page-realisations/page-realisations.component';




const routes: Routes = [
  { path: '', component: PageAccueilComponent},
  { path: 'bio', component: BioComponent },
  { path: 'contact', component: PageContactComponent },
  { path: 'realisations', component: PageRealisationsComponent }

/*

  { path: '/presentation', component: BioComponent },
  { path: '/contact', component: BioComponent },
  { path: '/realisations', component: BioComponent }
*/


];

@NgModule({
  declarations: [],
  imports: [
    RouterModule.forRoot(routes)
  ],
  exports: [
    RouterModule
  ]
})

export class AppRoutingModule { }

